<template>
  <div>
		<!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

		<!--  -->
		<el-card>
			<el-row>
				<el-col :span="8">
					<el-input placeholder="">
						<el-button slot="append" icon="el-icon-search"></el-button>
					</el-input>
				</el-col>
			</el-row>
			<!--  -->
			<el-table :data="orderlist" border stripe>
				<el-table-column type="index"></el-table-column>
				<el-table-column label="" prop="order_number"></el-table-column>
				<el-table-column label="" prop="order_price"></el-table-column>
				<el-table-column label="" prop="pay_status"></el-table-column>
				<el-table-column label="" prop="is_send">
					<template slot-scope="scope">
						<el-tag v-if="scope.row.pay_status === '1'" type="success"></el-tag>
						<el-tag v-else type="danger"></el-tag>
					</template>
				</el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
						{{scope.row.create_time | dataFormat}}
					</template>
				</el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
						<el-button type="primary" size="mini" icon="el-icon-edit"></el-button>
						<el-button type="success" size="mini" icon="el-icon-location"></el-button>
					</template>
				</el-table-column>
			</el-table>

			<!--  -->
			<el-pagination
				@size-change="handleSizeChange"
				@current-change="handleCurrentChange"
				:current-page="queryInfo.pagenum"
				:page-sizes="[5, 10, 15]"
				:page-size="queryInfo.pagesize"
				layout="total, sizes, prev, pager, next, jumper"
				:total="total">
			</el-pagination>
		</el-card>
	</div>
</template>

<script>
export default {
	data() {
		return {
			queryInfo: {
				query: '',
				pagenum: 1,
				pagesize: 10
			},
			total: 0,
			orderlist: []
		}
	},
	methods: {
		async getOrderList() {
			const {data: res} = await this.$http.get('orders', {params: this.queryInfo})
			if(res.meta.status !== 200) {
				return this.$message.error('')
			}
			this.total = res.data.total
			this.orderlist = res.data.goods
		},
		handleSizeChange(newSize) {
			this.queryInfo.pagesize = newSize
			this.getOrderList()
		},
		handleCurrentChange(newPage) {
			this.queryInfo.pagenum = newPage
			this.getOrderList()
		}
	},
	created() {
		this.getOrderList()
	}
}
</script>

<style lang="less" scoped>

</style>